<template>
    <div>
        <div class="cardtitle">{{title}}</div>
        <div class="pie" style="width:100%;height:400px;">
            <div v-if="!btn" class="back" @click="back()" ref="back">返回</div>
            <div :id="op.id" style="width:550px;height:400px;margin:0 auto;"></div>
        </div>
        <el-dialog title='' :visible.sync="dialogTableVisible" width="80%">
        <div class="cardtitle">{{tabletitle}}</div>
        <el-button type="primary" size="small" style="float:left;" icon="el-icon-document">导出</el-button>
        <el-table :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)">
            <el-table-column v-if="tableHeader[0]" :prop="tableProp[0]" :label="tableHeader[0]" sortable width="170"></el-table-column>
            <el-table-column v-if="tableHeader[1]" :prop="tableProp[1]" :label="tableHeader[1]" sortable width="170"></el-table-column>
            <el-table-column v-if="tableHeader[2]" :prop="tableProp[2]" :label="tableHeader[2]" sortable width="170"></el-table-column>
            <el-table-column v-if="tableHeader[3]" :prop="tableProp[3]" :label="tableHeader[3]" sortable width="170"></el-table-column>
            <el-table-column v-if="tableHeader[4]" :prop="tableProp[4]" :label="tableHeader[4]" sortable width="170"></el-table-column>
            <el-table-column v-if="tableHeader[5]" :prop="tableProp[5]" :label="tableHeader[5]" sortable width="170"></el-table-column>
            <el-table-column v-if="tableHeader[6]" :prop="tableProp[6]" :label="tableHeader[6]" sortable width="170"></el-table-column>
            <el-table-column v-if="tableHeader[7]" :prop="tableProp[7]" :label="tableHeader[7]" sortable width="170"></el-table-column>
            <el-table-column v-if="tableHeader[8]" :prop="tableProp[8]" :label="tableHeader[8]" sortable width="170"></el-table-column>
        </el-table>
        <el-pagination
            layout="prev, pager, next, sizes, total, jumper"
            :page-sizes="[5, 10, 15, 20]"
            :page-size="pagesize"   
            :total="tableData.length"
            @current-change="handleCurrentChange" 
            @size-change="handleSizeChange"
            style="margin-top:30px;">
        </el-pagination>
    </el-dialog>
    </div>
</template>

<script>
export default {
    props: ['op'],
    name: 'pie',
    data () {
        return {
            title:' ',
            btn:null,
            tabletitle:'',
            list: [],
            pagesize: 5,
            currpage: 1,
            dialogTableVisible: false,
            tableHeader:[],
            tableProp:[],
            tableData:[],
            pieData:'',
            paramName:'',
            ggxz:null,      //管、工
            bxjfzcbt:null,      //办学经费支出分类饼图
            option: {
                // title : {
                //     text: '某高校固定资产详情',
                //     subtext: '',
                //     x:'left'
                // },
                tooltip : {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                legend: {
                    orient: 'vertical',
                    right: '0',
                    bottom:'50%',
                    data: ['','']
                },
                color:['rgb(118,131,149)','rgb(124,179,73)','rgb(53,143,226)','rgb(80,80,80)','rgb(228,39,57)','rgb(128,146,149)','rgb(239,134,41)','rgb(240,240,240)',],
                series : [
                    {
                        name: '固定资产',
                        type: 'pie',
                        radius : '65%',
                        center: ['45%', '60%'],
                        data:[
                            {value:'', name:''},
                            {value:310, name:'非绿化'},
                        ],
                        itemStyle: {
                            normal:{
                                label:{
                                    show:true,
                                    formatter: "{b} :\n{c} \n({d}%)",
                                    position:"outside"
                                }
                            },
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            },
            option2: {
                title : {
                    text: '某高校固定资产详情',
                    subtext: '',
                    x:'left'
                },
                tooltip : {},
                series : [
                    {
                        name: '固定资产',
                        type: 'pie',
                        radius : '55%',
                        center: ['50%', '60%'],
                        data:[
                            {value:335, name:'一楼'},
                            {value:310, name:'二楼'},
                            {value:234, name:'三楼'}
                        ],
                        itemStyle: {
                            normal:{
                                label:{
                                    show:true,
                                    formatter: "{b} :\n{c} \n({d}%)",
                                    position:"outside"
                                }
                            },
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            }
        }
    },
    methods: {
        drawPie: function () {
        // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById(this.pieData.id))
            if(this.pieData.legend){
                this.option.legend.data = this.pieData.legend
            }
            this.option.series[0].name = this.pieData.series[0].name
            this.option.series[0].data = this.pieData.series[0].data
            this.option.tooltip.formatter = this.pieData.tooltip.formatter
            this.option.series[0].itemStyle.normal.label.formatter = this.pieData.series[0].itemStyle.normal.label.formatter
            // 绘制图表
            myChart.setOption(this.option,true)
        },
        back: function () {
            // this.drawPie()
            console.log(this.ggxz)
            if(!this.ggxz){
                this.$router.go(-1)
            }else{
                this.pieData = this.op
                this.title = this.op.title
                this.paramName = this.pieData.param
                this.drawPie()
                this.ggxz = null
            }
        },
        formatter(row, column) {
            return row.address;
        },
        handleCurrentChange(cpage) {
            this.currpage = cpage;
        },
        handleSizeChange(psize) {
            this.pagesize = psize;
        },
    },
    mounted () {
        this.pieData = this.op
        if(this.pieData.btn){
            this.btn = this.pieData.btn
        }
        console.log(this.pieData)
        this.title = this.pieData.title;
        this.drawPie()
        // 数据下潜
        this.paramName = this.pieData.param
        var _this = this
        let myChart = this.$echarts.init(document.getElementById(this.pieData.id))
        myChart.on('click', function (params) {
            console.log(params)
            if(_this.paramName == '1'){
                _this.ggxz = 'ggxz'
                if(params.dataIndex == '0' || params.dataIndex == '1'){
                    var data = {reg:'ggxz',style:'2'}
                    _this.tableHeader = ['职工号','姓名','性别','专业技术职务名称','职务名称','是否外聘','骨干教师','双师型教师','专业带头人','在职状态']
                    _this.tableProp = ['zgh','xm','xbm','zyjszwmc','zwmc','iswp','isggjzg','isssjzg','iszydtr','zzzt']
                }else{   //技的下钻
                    // var data = {reg:'',style:'2'}
                }
                
            }
            if(_this.paramName == 'nf49'){
                _this.bxjfzcbt = 'bxjfzcbt'
                // var data = {reg:'',style:'2'}
                // _this.tableHeader = []
                // _this.tableProp = []
            }
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
                data: data,
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    if(res.table){
                        _this.tabletitle = res.mess
                        _this.dialogTableVisible = true
                        _this.tableData = []
                        for(var i = 0;i < res.dataCount.length;i++){
                            if(res.dataCount[0].xbm){
                                if(res.dataCount[i].xbm == '1'){
                                    res.dataCount[i].xbm = '男'
                                }else{
                                    res.dataCount[i].xbm = '女'
                                }
                            }
                            if(res.dataCount[0].zzzt){
                                if(res.dataCount[i].zzzt == '1'){
                                    res.dataCount[i].zzzt = '在职'
                                }else{
                                    res.dataCount[i].zzzt = '离职'
                                }
                            }
                            if(res.dataCount[0].iswp){
                                if(res.dataCount[i].iswp == '1'){
                                    res.dataCount[i].iswp = '外聘'
                                }else{
                                    res.dataCount[i].iswp = '非外聘'
                                }
                            }
                            if(res.dataCount[i].isggjzg){
                                if(res.dataCount[i].isggjzg == '1'){
                                    res.dataCount[i].isggjzg = '是'
                                }else{
                                    res.dataCount[i].isggjzg = '否'
                                }
                            }else{
                                res.dataCount[i].isggjzg = '否'
                            }
                            if(res.dataCount[i].iszydtr){
                                if(res.dataCount[i].iszydtr == '1'){
                                    res.dataCount[i].iszydtr = '是'
                                }else{
                                    res.dataCount[i].iszydtr = '否'
                                }
                            }else{
                                res.dataCount[i].iszydtr = '否'
                            }
                            if(res.dataCount[i].isssjzg){
                                if(res.dataCount[i].isssjzg == '1'){
                                    res.dataCount[i].isssjzg = '是'
                                }else{
                                    res.dataCount[i].isssjzg = '否'
                                }
                            }else{
                                res.dataCount[i].isssjzg = '否'
                            }
                            _this.tableData.push(res.dataCount[i])
                        }
                        _this.ggxz = null;
                    }else{
                        _this.title = res.title
                        _this.xData = []
                        _this.yData = []
                        // if(res.dataCount){
                        //     for(var i = 0;i < res.dataCount.length;i++){
                        //         _this.xData.push(res.dataCount[i].mc)
                        //         _this.yData.push(res.dataCount[i].zcount)
                        //     }
                        //     _this.paramName = res.param1;
                        // }
                        
                    }
                }
            });
            // _this.option2.title.text = params.name
            // myChart.setOption(_this.option2,true)
        })
    },
    
}
</script>

<style scoped>
    .back{
        width: 100px;
        height: 20px;
        font-size: 16px;
        font-weight: 900;
        color: #029348;
        cursor: pointer;
        float: right;
        position: relative;
        right: 60px;
        top: 50px;
        z-index: 100;
    }

</style>



