<template>
    <div class="card1">
        <div class="card_left" :style="datainfo.style">
            <p v-if="windowWidth>1200" style="width:100%;padding:0 25px;">{{datainfo.title}}</p>
            <p v-else style="width:100%;padding:0 5px;">{{datainfo.title}}</p>
        </div>
        <div class="card_right">
            <div class="content">
                <div class="num">
                    {{datainfo.num}}<span style="font-size:16px;"> {{datainfo.type}}</span>
                </div>
                <div class="tips" v-if="datainfo.tips">参考标准：{{datainfo.tips}}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:['datainfo'],
    data(){
        return{
            windowWidth:0,
        }
    },
    mounted() {
        this.windowWidth = document.documentElement.offsetWidth;
        // console.log(this.windowWidth)
    },
}
</script>

<style scoped>
    .card1{
        /* min-width: 200px;
        max-width: 400px; */
        width: 100%;
        height: 120px;
        overflow: hidden;
        display: table;
    }
    .card_left{
        width: 35%;
        height: 120px;
        display: flex;
        align-items: center;
        float: left;
        background: rgba(118, 131, 149);
        color: #fff;
        font-size: 16px;
        font-weight: 900;
        line-height: 30px;
        text-shadow: 1.5px 1.5px 1px #000;
        text-align: center;
        word-wrap: break-word;
        word-break: normal;
    }
    .card_right{
        width: 65%;
        height: 120px;
        float: left;
        /* text-align: center; */
        position: relative;
    }
    .content{
        width: 100%;
        /* overflow: hidden; */
        text-align: left;
        display: inline-block;
    }
    .num{
        width: 100%;
        height: 120px;
        padding-left: 20px;
        line-height: 120px;
        color: #585858;
        font-size: 22px;
        font-weight: 900;
    }
    .tips{
        display: inline-block;
        /* width: 180px; */
        height: 30px;
        font-size: 11px;
        color: #8d8d8d;
        font-weight: 900;
        word-wrap: break-word;
        word-break: normal;
        position: absolute;
        bottom: 12px;
        left: 20px;
    }
</style>



