<template>
<div>
    <div class="cardtitle">{{title}}</div>
    <div id="lineCharts" style="width:100%;height:400px;">
        <div class="back" @click="back()" ref="back" v-if="windowWidth>1200" style="right:130px;">返回</div>
        <div class="back" @click="back()" ref="back" v-else style="right:10%;">返回</div>
        <div :id="op.id" ref="myLineChart" style="width:550px;height:400px;margin:0 auto;"></div>
    </div>
</div>
    
</template>

<script>
export default {
    props:['op'],
    data(){
        return{
            title:' ',
            windowWidth:0,
            option:{
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: null,
                    axisLabel: {
                        interval:0,
                        rotate:0,
                        fontSize: 12
                    }
                },
                yAxis: {
                    name:'',
                    type: 'value'
                },
                series: [{
                    data: null,
                    type: 'line',
                    color: '#029348',
                    smooth: true,
                    itemStyle: {
                    normal: {
                        label: {
                        show: true, //开启显示
                        position: 'top', //在上方显示
                        textStyle: { //数值样式
                            color: '#029348',
                            fontSize: 14
                        }
                        }
                    }
                    },
                }]
            }
        }
    },
    methods:{
        back(){
            this.drawLine();
        },
        drawLine(){
            let myLine = this.$echarts.init(document.getElementById(this.op.id));
            this.option.xAxis.data = this.op.xAxis.data
            this.option.yAxis.name = this.op.yAxis.name
            this.option.xAxis.axisLabel.rotate = this.op.xAxis.axisLabel.rotate
            this.option.series[0].data = this.op.series[0].data
            myLine.setOption(this.option)
        }
    },
    mounted(){
        this.title = this.op.title;
        this.windowWidth = document.documentElement.offsetWidth;
        // console.log(this.windowWidth)
        this.drawLine()
    }
}
</script>

<style scoped>
   #lineCharts{
        position: relative;
    }
    .back{
        font-size: 16px;
        font-weight: 900;
        color: #029348;
        cursor: pointer;
        text-align: left;
        float: right;
        position: absolute;
        right: 10%;
        top: 30px;
        z-index: 9999;
    }
</style>



